.Main {
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    width: 100%;
    align-items: center;
}
.Title-Text {
    font-size: 7rem;
    font-weight: 700;
    margin-bottom: 1rem;
    margin-top: 0;
    text-align: center;
}
.return-button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}


@media only screen and (max-width: 600px) {
    .Title-Text {
        font-size: 4rem;
    }
    .return-button {
        margin-top: 0.5rem;
        padding: 0.5rem 0.75rem;
        font-size: 0.75rem;
    }
}