.blog {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100%;
    width: 90%;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: var(--color-white);
    box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.3);
}
.blog.blog-column-display {
    display: flex;
    flex-direction: column;
}

.blog .text-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-right: 40px;
}

.blog p {
    margin: 0.5rem 0;
    text-align: justify;
    font-size: 20px;
}

.blog .text-image {
    height: 100%;
    margin: 10px;
}
.blog .text-image p {
    width: 400px;
}

.blog img {
    width: 400px;
    height: 100%;
    border-radius: 0.5rem;
}

.two-images {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 100%;
    width: 90%;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: var(--color-white);
}

.blog .images {
    display: flex;
    justify-content: space-around;
    padding: 40px;
}

@media only screen and (max-width: 600px) {
    .blog {
        flex-direction: column;
        width: 85%;
    }
    .blog img {
        width: 100%;
    }
    .blog .text-wrapper {
        padding-right: 0;
    }
    .blog .text-image p {
        width: 100%;
    }
    .two-images {
        flex-direction: row;
        width: 100%;
    }

    .blog .images {
        flex-direction: row;
        padding: 0;
    }

    .blog .images .text-image {
        margin-bottom: 20px;
    }

    .blog .images .text-image {
        padding: 10px;
    }
    .blog video {
        width: 100%;
        margin-bottom: 10px;
    }
}